import Command from "./Command";
class CommandIF extends Command {
    set true(block) {
        this._true = block;
    }
    set false(block) {
        this._false = block;
    }
    set value(value) {
        this._value = value;
    }
    get true() {
        return this._true;
    }
    get false() {
        return this._false;
    }
    get value() {
        return this._value;
    }
}
export default CommandIF;
