class Item {
    constructor(name = "", image = "") {
        this.setItem(name, image);
    }
    setItem(name, image) {
        this._name = name;
        this._image = image;
    }
    remove() {
        this.element.remove();
    }
    get name() {
        return this._name;
    }
    get image() {
        return this._image;
    }
    set element(element) {
        this._element = element;
    }
    get element() {
        return this._element;
    }
    static Queijo() {
        return new Item("Queijo", "queijo.png");
    }
    static Pao() {
        return new Item("Pao", "pao.png");
    }
    static Presunto() {
        return new Item("Presunto", "presunto.png");
    }
    static Ovo() {
        return new Item("Ovo", "ovo.png");
    }
    static Leite() {
        return new Item("Leite", "leite.png");
    }
    static Farinha() {
        return new Item("Farinha", "farinha.png");
    }
    static Chocolate() {
        return new Item("Chocolate", "chocolate.png");
    }
}
export default Item;
